<template>
  <div class="style">
    <top></top>
    <div class="style_sub">
      <div class="style_subject">
        <span class="style_span">主题色</span>
        <Tooltip placement="right" content="主题色将更改导航栏、图标、按钮颜色。" class="Tooltip">
          <Icon type="ios-help" size="20" color="#a9aeb2"></Icon>
        </Tooltip>
      </div>
      <div class="color_s">
        <div class="outer" v-for="(item,key) in lists" @click="select(key)">
          <div class="inner" :style="{background:item.color}">
            <Icon type="checkmark-round" :color="item.icon" v-if="item.show"></Icon>
          </div>
        </div>
      </div>
      <p class="xia"></p>
      <h3 class="style_h3">商品布局</h3>
      <div class="buju">
        <RadioGroup v-model="style" @on-change="goodsStyle">
          <Radio label="0">
            <span>方格布局</span>
          </Radio>
          <Radio label="1">
            <span>列表布局</span>
          </Radio>
          <Radio label="2">
            <span>滑动布局(需添加商品分类)</span>
          </Radio>
        </RadioGroup>
      </div>
      <p class="xia"></p>
      <div>
        <h3 class="style_h3">搜索功能</h3>
        <div class="buju">
          <Checkbox v-model="single" @on-change="search" false-value="0" true-value="1"><span style="font-size: 0.14rem;margin-left: 0.05rem">启用搜索</span></Checkbox><span style="font-size: 0.14rem">(建议搜索功能在商品数量众多情况时开启)</span>
        </div>
      </div>
      <p class="xia"></p>
      <div>
        <h3 class="style_h3">在线客服</h3>
        <div class="buju">
          <Checkbox v-model="singleKu" @on-change="online" false-value="0" true-value="1"><span style="font-size: 0.14rem;margin-left: 0.05rem">在商品购买页显示在线客服按钮，顾客可以发送微信消息。</span></Checkbox>
          <a href="javascript:void(0)" style="font-size: 0.14rem">
            <Icon type="person-add"></Icon> 设置在线客服</a>
        </div>
      </div>
      <p class="xia"></p>
      <h3 class="style_h3">轮播图</h3>

      <div style="margin-bottom:25px" class="buju">
        <ul>
          <div><span style="color: #B8B8B8;">*上传轮播图时，必须选择链接类型，删除和上传完之后需要保存</span></div>
          <li v-for="(item,key) in items" style="margin: 10px 0">
            <p style="margin-bottom: 0.1rem" v-if="key === 0">
              <span style="width: 1rem;cursor: pointer;display: inline-block;margin-right: 0.1rem">图片</span>
              <span style="width: 1rem;cursor: pointer;display: inline-block">链接类型</span>
            </p>
            <div class="style_p">
              <img :src="item.pic" alt="暂无图片信息" style="width: 1rem;height: 0.32rem;margin-right: 0.1rem;cursor: pointer" @click="changePhoto(key)">

              <Select v-model="item.link" style="width:150px">
                <Option v-for="(list,key) in item.list" :value="list.value" :key="list.value">{{ list.label }}</Option>
              </Select>
              <div class="input" style="margin-left: 0.1rem" v-show="item.link==='2'">
                <Select v-model="item.good" style="width:300px">
                  <Option v-for="(list,key) in goods" :value="list.value" :key="list.value">{{ list.label }}</Option>
                </Select>
              </div>
              <div class="input" style="margin-left: 0.1rem" v-show="item.link==='3'">
                <Select v-model="item.good" style="width:300px" @change.native="change">
                  <Option v-for="(list,key) in sorts" :value="list.value" :key="list.value">{{ list.label }}</Option>
                </Select>
              </div>
              <div class="input" style="margin-left: 0.1rem" v-show="item.link==='4'">
                <Select v-model="item.good" style="width:300px">
                  <Option v-for="(list,key) in pages" :value="list.value" :key="list.value">{{ list.label }}</Option>
                </Select>
              </div>
              <Icon type="android-delete" size="20" style="cursor: pointer;margin-left: 0.1rem" @click.native="deleted(key)"></Icon>
            </div>
          </li>
        </ul>
        <a href="javascript:void(0)" class="add" @click="add">
          <Icon type="android-add-circle"></Icon> 添加</a>
        <div style="margin-top:0.2rem ;">
          <a href="javascript:void(0)" class="a" @click="save" v-if="!load">保存</a>
          <a href="javascript:void(0)" class="a" v-if="load">保存 <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
        </div>
      </div>

    </div>

    <modal ref="upload"></modal>
  </div>
</template>
<script>
  import top from '../top.vue'
  import modal from '../upload/modal.vue'
  export default {
    data() {
      return {
        load: false,
        lists: [{
            color: 'white',
            show: true,
            icon: 'black'
          },
          {
            color: 'black',
            show: false,
            icon: 'white'
          },
          {
            color: '#3790f4',
            show: false,
            icon: 'white'
          },
          {
            color: '#ff8635',
            show: false,
            icon: 'white'
          },
          {
            color: '#2a9a3d',
            show: false,
            icon: 'white'
          },
          {
            color: '#ed5ec0',
            show: false,
            icon: 'white'
          },
          {
            color: '#9f56dd',
            show: false,
            icon: 'white'
          },
          {
            color: '#ad855e',
            show: false,
            icon: 'white'
          },
          {
            color: '#00c1e4',
            show: false,
            icon: 'white'
          },
          {
            color: '#f12c20',
            show: false,
            icon: 'white'
          },
        ],
        style: '0',
        single: "1",
        singleKu: "1",
        src: "",
        id: "",
        goods: [],
        good: '',
        sorts: [],
        sort: '',
        pages: [],
        page: '',
        item: [],
      }
    },
    methods: {
      //          设置style
      setGoodsStyle(style, value) {
        if(style === "theme") {
          window.frames[0].postMessage({
            tabBar: {
              selectedColor: this.lists[value].color
            },
            window: {
              navigationBarBackgroundColor: this.lists[value].color
            }
          }, "*");
        } else if(style === "layout") {
          //          布局
          window.frames[0].postMessage({
            attr: {
              layout: value
            }
          }, "*");
        } else {
          //        搜索
          let search = value === '0';
          window.frames[0].postMessage({
            attr: {
              searchEnable: search
            }
          }, "*");
        }
        this.post("custom/Loop/setAppStyle", {
          style,
          value
        }).then(res => {
          if(res.data.code === 10000) {
            const content = res.data.msg_test;
            this.$Modal.success({
              title: "设置成功",
              content: content
            });
          } else {
            const content = res.data.msg_test + "请重新设置";
            this.$Modal.error({
              title: "设置失败",
              content: content
            });
          }
        })
      },
      goodsStyle() {
        this.setGoodsStyle("layout", this.style);
      },
      search() {
        this.setGoodsStyle("search", this.single);
      },
      online() {
        this.setGoodsStyle("service", this.singleKu);
      },
      change() {

      },
      changePhoto(key) {
        this.$refs.upload.modal = true;
        let obj = {};
        obj["flag"] = 2;
        obj["id"] = key;
        this.$store.commit('setCarouselFlag', obj);
        window.frames[0].postMessage(obj, '*');
        this.$store.dispatch('goodsPhoto', 3);

      },
      select(key) {
        this.lists.forEach(item => {
          item.show = false;
        });
        this.lists[key].show = true;
        this.setGoodsStyle("theme", key);
      },
      deleted(key) {
        this.$store.commit('deleteCarousel', key);
      },
      add() {
        this.$refs.upload.modal = true;
        let obj = {};
        obj["flag"] = 1;
        obj["id"] = '';
        this.$store.dispatch('goodsPhoto', 3);
        this.$store.commit('setCarouselFlag', obj);
        window.frames[0].postMessage(obj, '*');
      },
      save() {
        this.load = true;
        let arr = [];
        this.items.forEach((item, key) => {
          if(item.link === '1') {
            let obj = {};
            obj["type"] = item.link;
            obj["pic"] = item.pic.slice(29);
            arr.push(obj);
          }
          if(item.link === "2") {
            let obj = {};
            obj["type"] = item.link;
            obj["pic"] = item.pic.slice(29);
            obj["spec"] = this.goods[this.items[key]["good"] - 1].id;
            arr.push(obj);
          }
          if(item.link === "3") {
            let obj = {};
            obj["type"] = item.link;
            obj["pic"] = item.pic.slice(29);
            obj["spec"] = this.sorts[this.items[key]["good"] - 1].id;
            arr.push(obj);
          }
          if(item.link === "4") {
            let obj = {};
            obj["type"] = item.link;
            obj["pic"] = item.pic.slice(29);
            obj["spec"] = this.pages[this.items[key]["good"] - 1].id;
            arr.push(obj);
          }
        });
        this.post('custom/Loop/setLoopImg', {
          info: JSON.stringify(arr)
        }).then(res => {
          this.load = false;
          if(res.data.code === 10000) {
            const content = res.data.msg_test;
            this.$Modal.success({
              title: "添加成功",
              content: content
            });
          } else {
            const content = res.data.msg_test + "请重新添加"
            this.$Modal.error({
              title: "添加失败",
              content: content
            });
          }
        })
      }
    },
    computed: {
      items() {
        return this.$store.getters.getCarousel;
      }
    },
    created() {
      let that = this;
      //      获取所有商品信息
      function getGoodsInfo() {
        return that.post('custom/Loop/getAllShopInfo', {})
      }
      //      获取所有的分类信息
      function getGoodsSorts() {
        return that.post('custom/Loop/getAllCateInfo', {})
      }
      //      获取所有页面信息
      function getPages() {
        return that.post('custom/Loop/getAllPageInfo', {})
      }
      this.all([getGoodsInfo(), getGoodsSorts(), getPages()]).then(res => {
        if(res[0].data.code === 10000) {
          let j = 1;
          for(let i in res[0].data.data) {
            let obj = {};
            obj["id"] = i;
            obj["label"] = res[0].data.data[i];
            obj["value"] = `${j}`;
            this.goods.push(obj);
            j++;
          }
        }
        if(res[1].data.code === 10000) {
          let j = 1;
          for(let i in res[1].data.data) {
            let obj = {};
            obj["id"] = i;
            obj["label"] = res[1].data.data[i];
            obj["value"] = `${j}`;
            this.sorts.push(obj);
            j++;
          }
        }
        if(res[2].data.code === 10000) {
          let j = 1;
          for(let i in res[2].data.data) {
            let obj = {};
            obj["id"] = i;
            obj["label"] = res[2].data.data[i];
            obj["value"] = `${j}`;
            this.pages.push(obj);
            j++;
          }
        }
        this.post('custom/Loop/getLoopImgList', {}).then(res => {
          if(res.data.code === 10000) {
            let arr = JSON.parse(res.data.data.content);
            arr.forEach((item, key) => {
              item.pic = "http://weapp.xiguawenhua.com/" + item.pic;
              item["list"] = [{
                value: '1',
                label: '无连接'
              }, {
                value: '2',
                label: '商品',
              }, {
                value: '3',
                label: '分类',
              }, {
                value: '4',
                label: '界面',
              }];
              if(item.type === '1') {
                item["link"] = "1";
              }
              if(item.type === '2') {
                item["link"] = "2";
                this.goods.forEach((list, key) => {
                  if(list.id === item.spec) {
                    item["good"] = `${key+1}`;
                  }
                });
              }
              if(item.type === '3') {
                item["link"] = "3";
                this.sorts.forEach((list, key) => {
                  if(list.id === item.spec) {
                    item["good"] = `${key+1}`;
                  }
                });
              }
              if(item.type === '4') {
                item["link"] = "4";
                this.pages.forEach((list, key) => {
                  if(list.id === item.spec) {
                    item["good"] = `${key+1}`;
                  }
                });
              }
            });
            this.$store.state.carousel = arr;
          }
        })

      })
    },
    mounted() {
      this.post("custom/loop/getAppStyle", {}).then(res => {
        if(res.data.code === 10000) {
          this.style = `${res.data.data.layout}`;
          this.single = `${res.data.data.search}`;
          this.singleKu = `${res.data.data.on_service}`;
          this.lists.forEach(item => {
            item.show = false;
          });
          this.lists[res.data.data.theme]["show"] = true;
          window.frames[0].postMessage({
            tabBar: {
              selectedColor: this.lists[res.data.data.theme].color
            },
            window: {
              navigationBarBackgroundColor: this.lists[res.data.data.theme].color
            }
          }, "*");
        } else {

        }
      })
    },
    components: {
      top,
      modal
    }
  }
</script>
<style scoped="scoped">
  @import url('../../../static/style.css');
</style>


